body {
    font-family: Arial, sans-serif;
    margin: 0;
    background-color: #2d4a78;
}

.dashboard-container {
    text-align: center;
    color: white;
}

.dashboard-content {
    padding: 20px;
}

h1 {
    font-size: 36px;
    margin-bottom: 20px;
}

.metrics-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
}

.metric-card {
    background-color: #3b5998;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 250px;
    text-align: center;
}

.metric-card h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.metric-card p {
    font-size: 18px;
}

/* Latest News Section */
.latest-news {
    margin-top: 40px;
    text-align: center;
}

.latest-news h2 {
    font-size: 28px;
    margin-bottom: 20px;
}

.news-image {
    width: 100%;
    max-width: 600px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease;
}

.news-image:hover {
    transform: scale(1.05);
}

.news-link {
    display: inline-block;
    text-decoration: none;
    color: inherit;
}
